




































































































import Vue from 'vue';
import {mapState} from 'vuex'
import store from './store'
export default Vue.extend({
  name: 'App',
  store,
  components: {
  },
  data: () => ({
      activeBtn:1,
      drawer: false,
      select:null,
      items:[],
      search:null,
      loading:null,
      config:true,
      usephoneLayout:null,
      windowSize: { x: 0,y:0}
  }),
  methods:{
    gotoMain(){
      this.$router.push('/')
    },
    onresize(){
        this.windowSize =  { x: window.innerWidth, y: window.innerHeight }
        this.usephoneLayout = this.windowSize.x<600 ? true : false
    }
  },
  created(){
    this.windowSize =  { x: window.innerWidth, y: window.innerHeight }
    this.usephoneLayout = this.windowSize.x<600 ? true : false
  },
  computed:{
    ...mapState({
      Navigation : store => store['Navigation']
    })
  }
});
